import React, { useState, forwardRef } from "react";

import "../../App.css";
import "./Portfolio.css";

import { ProjectCard } from "../";
import { useWindowDimensions } from "../../hooks";

import { projects } from "../../store";

const Portfolio = forwardRef((props, ref) => {
    const windowSize = useWindowDimensions();

    const [displayProjects, setDisplayProjects] = useState(true);

    let timeInc = 0;
    let projectsMarkup = projects.map(proj => {
        timeInc += 100;
        return <ProjectCard project={proj} fadeInTime={timeInc} display={true} />
    })

    return (
        <div className="section portfolio-bkg" style={{ height: windowSize.height }} ref={ref}>
            <span className="h1">Portfolio</span>
            <div className="projects-container">
                {projectsMarkup}
            </div>
        </div>
    );
})

export default Portfolio;